import { GetPublicMemosParams, PaginatedMemoListDto } from "../types/dto"

const API_BASE_URL = "https://ximing.ren"
const USER_UID = "uih8z8fcputbct33fb9nyidz"

export interface ApiError {
  status: number
  message: string
}

export class MemosApiError extends Error {
  status: number

  constructor(message: string, status: number) {
    super(message)
    this.name = "MemosApiError"
    this.status = status
  }
}

export const getPublicMemos = async (
  params: GetPublicMemosParams = {}
): Promise<PaginatedMemoListDto> => {
  const {
    page = 1,
    limit = 20,
    sortBy = "createdAt",
    sortOrder = "desc",
  } = params

  const queryParams = new URLSearchParams({
    page: page.toString(),
    limit: limit.toString(),
    sortBy,
    sortOrder,
  })

  const url = `${API_BASE_URL}/api/v1/memos/public/${USER_UID}?${queryParams}`

  try {
    const response = await fetch(url)

    if (!response.ok) {
      if (response.status === 400) {
        throw new MemosApiError("", 400)
      }
      if (response.status === 500) {
        throw new MemosApiError("", 500)
      }
      throw new MemosApiError(
        `HTTP error! status: ${response.status}`,
        response.status
      )
    }

    const data: { code: number; data: PaginatedMemoListDto; msg: string } =
      await response.json()
    if (data.code !== 0) {
      throw new MemosApiError(data.msg, data.code)
    }
    return data.data as PaginatedMemoListDto
  } catch (error) {
    if (error instanceof MemosApiError) {
      throw error
    }
    if (error instanceof TypeError && error.message.includes("fetch")) {
      throw new MemosApiError("", 0)
    }
    throw new MemosApiError(
      error instanceof Error ? error.message : "",
      0
    )
  }
}
