import React, { useMemo } from "react"
import dayjs from "dayjs"
import { MemoListItemDto } from "../../types/dto"
import { IconWrapper } from "../icons"
import { Hash } from "@phosphor-icons/react"

interface MemoCardProps {
  memo: MemoListItemDto
  index?: number
}

export const MemoCard: React.FC<MemoCardProps> = ({ memo, index = 0 }) => {
  // Convert markdown-like content to HTML
  const contentHtml = useMemo(() => {
    return (memo?.content || "")
      .replace(/</g, "&lt;")
      .replace(/>/g, "&gt;")
      .replace(/\*\*(.+?)\*\*/g, "<strong>$1</strong>")
      .replace(/\*(.+?)\*/g, "<em>$1</em>")
      .replace(/`(.+?)`/g, '<code class="bg-bg-secondary px-1 py-0 rounded text-[13px] font-mono">$1</code>')
      .replace(/\n/g, "<br />")
  }, [memo?.content])

  // Format date with relative time
  const formattedDate = useMemo(() => {
    const date = dayjs(memo.createdAt)
    const now = dayjs()
    const diffDays = now.diff(date, "day")

    if (diffDays === 0) {
      return " " + date.format("HH:mm")
    } else if (diffDays === 1) {
      return " " + date.format("HH:mm")
    } else if (diffDays < 7) {
      return diffDays + " "
    } else if (diffDays < 30) {
      return Math.floor(diffDays / 7) + " "
    } else {
      return date.format("YYYY-MM-DD")
    }
  }, [memo.createdAt])

  return (
    <article className="p-3.5 sm:p-4">
      {/* Header */}
      <div className="flex items-center justify-between mb-2">
        <div className="flex items-center gap-1.5">
          {/* Date badge -  */}
          <div className="inline-flex items-center px-2 py-0.5 rounded-md bg-bg-secondary/80 text-[11px] text-text-muted">
            {formattedDate}
          </div>

          {/* Content type indicator */}
          {memo.type !== "text" && (
            <span className="text-[11px] text-text-muted">
              {memo.type === "audio" ? " " : " "}
            </span>
          )}
        </div>
      </div>

      {/* Content */}
      <div
        className="text-text-primary text-[14px] leading-7 max-w-none"
        dangerouslySetInnerHTML={{ __html: contentHtml }}
      />

      {/* Tags -  */}
      {memo?.tags && memo.tags.length > 0 && (
        <div className="flex flex-wrap gap-1.5 mt-3 pt-2">
          {memo.tags?.map((tag) => (
            <span
              key={tag}
              className="inline-flex items-center gap-0.5 px-2 py-0.5 text-[11px] text-text-subtle hover:text-accent transition-colors cursor-pointer bg-bg-secondary/50 rounded"
            >
              <IconWrapper icon={Hash} size={10} />
              {tag}
            </span>
          ))}
        </div>
      )}
    </article>
  )
}

export default MemoCard
