import React from "react"
import { AttachmentDto } from "../../types/dto"
import { IconWrapper } from "../icons"
import { SpeakerHigh } from "@phosphor-icons/react"

interface AudioAttachmentsProps {
  attachments: AttachmentDto[]
}

// Format duration in seconds to mm:ss
const formatDuration = (seconds: number): string => {
  const mins = Math.floor(seconds / 60)
  const secs = Math.floor(seconds % 60)
  return `${mins.toString().padStart(2, "0")}:${secs.toString().padStart(2, "0")}`
}

export const AudioAttachments: React.FC<AudioAttachmentsProps> = ({
  attachments,
}) => {
  // Filter audio attachments
  const audioAttachments = attachments?.filter((att) =>
    att.type?.startsWith("audio/")
  ) || []

  if (audioAttachments.length === 0) {
    return null
  }

  return (
    <div className="mt-3 space-y-2">
      {audioAttachments.map((attachment) => (
        <div
          key={attachment.attachmentId}
          className="bg-bg-secondary rounded-xl p-3 sm:p-4"
        >
          {/* Header with icon and filename */}
          <div className="flex items-center gap-3 mb-3">
            <div className="w-10 h-10 rounded-full bg-blue-500/10 flex items-center justify-center flex-shrink-0">
              <IconWrapper
                icon={SpeakerHigh}
                size={20}
                className="text-blue-500"
              />
            </div>
            <div className="flex-1 min-w-0">
              <p className="text-sm font-medium text-text-primary truncate">
                {attachment.name}
              </p>
              {attachment.properties?.duration && (
                <p className="text-xs text-text-muted">
                  {formatDuration(attachment.properties.duration)}
                </p>
              )}
            </div>
          </div>

          {/* Audio player */}
          <audio
            controls
            className="w-full h-10"
            preload="metadata"
          >
            <source src={attachment.url} type={attachment.type} />
            
          </audio>
        </div>
      ))}
    </div>
  )
}

export default AudioAttachments
