import React, { useState, useEffect, useCallback } from "react"
import { PageProps, graphql } from "gatsby"
import InfiniteScroll from "react-infinite-scroll-component"
import { IconWrapper } from "../components/icons"
import Layout from "../components/layout"
import SEO from "../components/seo"
import {
  MemoCard,
  ImageAttachments,
  AudioAttachments,
  VideoAttachments,
  MixedAttachments,
} from "../components/think"
import { getPublicMemos, MemosApiError } from "../api/memos"
import { MemoListItemDto } from "../types/dto"
import {
  ArrowClockwise,
  Spinner,
  Brain,
  Warning,
} from "@phosphor-icons/react"

interface ThinkPageData {
  site: {
    siteMetadata: {
      title: string
    }
  }
}

// Skeleton loader component - 
const MemoSkeleton: React.FC = () => (
  <div className="bg-background-card rounded-2xl p-5 animate-pulse">
    <div className="flex items-center gap-3 mb-3">
      <div className="h-3.5 w-20 bg-bg-secondary rounded" />
    </div>
    <div className="space-y-2">
      <div className="h-3.5 bg-bg-secondary rounded w-full" />
      <div className="h-3.5 bg-bg-secondary rounded w-5/6" />
      <div className="h-3.5 bg-bg-secondary rounded w-4/6" />
    </div>
  </div>
)

// Error state component
const ErrorState: React.FC<{
  message: string
  onRetry: () => void
}> = ({ message, onRetry }) => (
  <div className="flex flex-col items-center justify-center py-16 px-4">
    <div className="w-16 h-16 rounded-full bg-error-subtle flex items-center justify-center mb-4">
      <IconWrapper icon={Warning} size={32} className="text-error" />
    </div>
    <h3 className="text-lg font-semibold text-text-primary mb-2">
      
    </h3>
    <p className="text-text-secondary text-center mb-6 max-w-md">
      {message || ""}
    </p>
    <button
      onClick={onRetry}
      className="inline-flex items-center gap-2 px-5 py-2.5 rounded-xl bg-accent text-accent-foreground font-medium hover:bg-accent-hover transition-all duration-200 shadow-lg shadow-accent/20"
    >
      <IconWrapper icon={ArrowClockwise} size={18} weight="bold" />
      
    </button>
  </div>
)

// Empty state component
const EmptyState: React.FC = () => (
  <div className="flex flex-col items-center justify-center py-16 px-4">
    <div className="w-20 h-20 rounded-full bg-bg-secondary flex items-center justify-center mb-4 ring-4 ring-bg-primary">
      <IconWrapper icon={Brain} size={40} className="text-text-muted" />
    </div>
    <h3 className="text-xl font-semibold text-text-primary mb-2">
      
    </h3>
    <p className="text-text-secondary text-center max-w-sm">
      
    </p>
  </div>
)

// Single memo card with attachments - 
const MemoItem: React.FC<{
  memo: MemoListItemDto
  index: number
}> = ({ memo, index }) => {
  return (
    <article
      className="group bg-background-card rounded-2xl overflow-hidden transition-all duration-300 hover:shadow-md"
      style={{ animationDelay: `${index * 0.05}s` }}
    >
      <MemoCard memo={memo} index={index} />

      {/* Attachments - /4 */}
      {memo?.attachments && memo.attachments.length > 0 && (
        <div className="px-4 sm:px-6 pb-4 sm:pb-5 -mt-2 space-y-3">
          {/*  -  */}
          <AudioAttachments attachments={memo.attachments} />
          {/*  - 4 */}
          <MixedAttachments attachments={memo.attachments} />
        </div>
      )}
    </article>
  )
}

// Page header - 
const PageHeader: React.FC = () => {
  return (
    <div className="mb-8 text-center">
      {/* Icon */}
      <div className="inline-flex items-center justify-center w-10 h-10 rounded-xl bg-accent/10 text-accent mb-3">
        <IconWrapper icon={Brain} size={20} weight="duotone" />
      </div>

      {/* Title */}
      <h1
        className="text-2xl font-bold text-text-primary mb-2 tracking-tight"
        style={{ fontFamily: '"Noto Serif SC", Georgia, serif' }}
      >
        Think
      </h1>

      {/* Subtitle */}
      <p className="text-text-secondary text-sm max-w-md mx-auto">
        
      </p>
    </div>
  )
}

// Main page component
const ThinkPage: React.FC<PageProps<ThinkPageData>> = ({ data, location }) => {
  const [memos, setMemos] = useState<MemoListItemDto[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [page, setPage] = useState(1)
  const [hasMore, setHasMore] = useState(true)

  const siteTitle = data.site.siteMetadata.title

  // Fetch memos from API
  const fetchMemos = useCallback(async (pageNum: number, reset: boolean = false) => {
    try {
      setLoading(true)
      if (reset) {
        setError(null)
      }

      const response = await getPublicMemos({
        page: pageNum,
        limit: 20,
        sortBy: "createdAt",
        sortOrder: "desc",
      })

      const items = response.items || []

      if (reset) {
        setMemos(items)
      } else {
        setMemos((prev) => [...prev, ...items])
      }

      setHasMore(pageNum < (response.pagination?.totalPages || 1))
      setPage(pageNum)
    } catch (err) {
      if (err instanceof MemosApiError) {
        setError(err.message)
      } else {
        setError("")
      }
    } finally {
      setLoading(false)
    }
  }, [])

  // Initial load
  useEffect(() => {
    fetchMemos(1, true)
  }, [fetchMemos])

  // Load more handler
  const loadMore = useCallback(() => {
    if (!loading && hasMore) {
      fetchMemos(page + 1, false)
    }
  }, [fetchMemos, loading, hasMore, page])

  // Retry handler
  const handleRetry = useCallback(() => {
    setMemos([])
    setPage(1)
    setHasMore(true)
    fetchMemos(1, true)
  }, [fetchMemos])

  return (
    <Layout location={location} title={siteTitle}>
      <SEO title="Think" description=" - " />

      <div className="max-w-2xl mx-auto">
        {/* Page Header */}
        <PageHeader />

        {/* Error State */}
        {error && memos?.length === 0 && (
          <ErrorState message={error} onRetry={handleRetry} />
        )}

        {/* Loading Skeleton - Initial */}
        {loading && memos?.length === 0 && !error && (
          <div className="space-y-4">
            {[...Array(3)].map((_, i) => (
              <MemoSkeleton key={i} />
            ))}
          </div>
        )}

        {/* Memo List with Infinite Scroll */}
        {!error && memos?.length > 0 && (
          <InfiniteScroll
            dataLength={memos?.length || 0}
            next={loadMore}
            hasMore={hasMore}
            loader={
              <div className="py-8 text-center">
                <IconWrapper
                  icon={Spinner}
                  size={24}
                  className="animate-spin text-accent mx-auto"
                />
                <p className="text-text-muted text-sm mt-2">...</p>
              </div>
            }
            endMessage={
              <div className="mt-10 text-center">
                <p className="text-text-muted text-sm">
                    {memos?.length || 0}  
                </p>
              </div>
            }
          >
            <div className="space-y-4">
              {memos?.map((memo, index) => (
                <MemoItem
                  key={memo.memoId}
                  memo={memo}
                  index={index}
                />
              ))}
            </div>
          </InfiniteScroll>
        )}

        {/* Empty State */}
        {!loading && !error && memos?.length === 0 && <EmptyState />}
      </div>
    </Layout>
  )
}

export default ThinkPage

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
