import React, { useState, useCallback } from "react"
import { AttachmentDto } from "../../types/dto"
import { IconWrapper } from "../icons"
import { X, CaretLeft, CaretRight, Play } from "@phosphor-icons/react"

interface MixedAttachmentsProps {
  attachments: AttachmentDto[]
}

// Format duration in seconds to mm:ss
const formatDuration = (seconds: number): string => {
  const mins = Math.floor(seconds / 60)
  const secs = Math.floor(seconds % 60)
  return `${mins.toString().padStart(2, "0")}:${secs.toString().padStart(2, "0")}`
}

export const MixedAttachments: React.FC<MixedAttachmentsProps> = ({
  attachments,
}) => {
  const [lightboxOpen, setLightboxOpen] = useState(false)
  const [currentIndex, setCurrentIndex] = useState(0)
  const [activeVideo, setActiveVideo] = useState<string | null>(null)

  // Filter image and video attachments
  const mediaAttachments = attachments?.filter((att) =>
    att.type?.startsWith("image/") || att.type?.startsWith("video/")
  ) || []

  if (mediaAttachments.length === 0) {
    return null
  }

  const openLightbox = (index: number) => {
    setCurrentIndex(index)
    setLightboxOpen(true)
    document.body.style.overflow = "hidden"
  }

  const closeLightbox = useCallback(() => {
    setLightboxOpen(false)
    setActiveVideo(null)
    document.body.style.overflow = "auto"
  }, [])

  const goToPrevious = useCallback(
    (e: React.MouseEvent) => {
      e.stopPropagation()
      setCurrentIndex((prev) =>
        prev === 0 ? mediaAttachments.length - 1 : prev - 1
      )
      setActiveVideo(null)
    },
    [mediaAttachments.length]
  )

  const goToNext = useCallback(
    (e: React.MouseEvent) => {
      e.stopPropagation()
      setCurrentIndex((prev) =>
        prev === mediaAttachments.length - 1 ? 0 : prev + 1
      )
      setActiveVideo(null)
    },
    [mediaAttachments.length]
  )

  const handleKeyDown = useCallback(
    (e: KeyboardEvent) => {
      if (!lightboxOpen) return
      if (e.key === "Escape") closeLightbox()
      if (e.key === "ArrowLeft") {
        setCurrentIndex((prev) =>
          prev === 0 ? mediaAttachments.length - 1 : prev - 1
        )
        setActiveVideo(null)
      }
      if (e.key === "ArrowRight") {
        setCurrentIndex((prev) =>
          prev === mediaAttachments.length - 1 ? 0 : prev + 1
        )
        setActiveVideo(null)
      }
    },
    [lightboxOpen, closeLightbox, mediaAttachments.length]
  )

  React.useEffect(() => {
    document.addEventListener("keydown", handleKeyDown)
    return () => document.removeEventListener("keydown", handleKeyDown)
  }, [handleKeyDown])

  return (
    <>
      <div className="grid grid-cols-4 gap-1.5">
        {mediaAttachments.map((attachment, index) => (
          <button
            key={attachment.attachmentId}
            onClick={() => openLightbox(index)}
            className="block w-full group relative aspect-square overflow-hidden rounded-lg bg-bg-secondary"
            aria-label={` ${attachment.type?.startsWith("video/") ? "" : ""} ${index + 1}`}
          >
            {attachment.type?.startsWith("video/") ? (
              <>
                <video
                  src={attachment.url}
                  className="w-full h-full object-cover"
                  preload="metadata"
                />
                {/* Video play overlay */}
                <div className="absolute inset-0 flex items-center justify-center bg-black/30">
                  <div className="w-10 h-10 rounded-full bg-white/90 flex items-center justify-center">
                    <IconWrapper icon={Play} size={20} className="text-text-primary ml-0.5" weight="fill" />
                  </div>
                </div>
                {/* Duration badge */}
                {attachment.properties?.duration && (
                  <div className="absolute bottom-1.5 right-1.5 px-1.5 py-0.5 rounded bg-black/60 text-white text-[10px]">
                    {formatDuration(attachment.properties.duration)}
                  </div>
                )}
              </>
            ) : (
              <>
                <img
                  src={attachment.thumbnailUrl || attachment.url}
                  alt={attachment.name}
                  className="w-full h-full object-cover transition-transform duration-700 group-hover:scale-105"
                  loading="lazy"
                />
                {/* Hover overlay */}
                <div className="absolute inset-0 bg-black/0 group-hover:bg-black/20 transition-colors duration-300" />
              </>
            )}
          </button>
        ))}
      </div>

      {lightboxOpen && (
        <Lightbox
          attachment={mediaAttachments[currentIndex]}
          currentIndex={currentIndex}
          totalCount={mediaAttachments.length}
          onClose={closeLightbox}
          onPrevious={goToPrevious}
          onNext={goToNext}
        />
      )}
    </>
  )
}

// Lightbox component
interface LightboxProps {
  attachment: AttachmentDto
  currentIndex: number
  totalCount: number
  onClose: () => void
  onPrevious: (e: React.MouseEvent) => void
  onNext: (e: React.MouseEvent) => void
}

const Lightbox: React.FC<LightboxProps> = ({
  attachment,
  currentIndex,
  totalCount,
  onClose,
  onPrevious,
  onNext,
}) => {
  const isVideo = attachment.type?.startsWith("video/")

  return (
    <div
      className="fixed inset-0 z-50 flex items-center justify-center bg-black/95 backdrop-blur-sm"
      onClick={onClose}
    >
      {/* Close button */}
      <button
        onClick={onClose}
        className="absolute top-4 right-4 p-2 rounded-full bg-white/10 hover:bg-white/20 text-white transition-colors z-10"
        aria-label=""
      >
        <IconWrapper icon={X} size={24} weight="bold" />
      </button>

      {/* Media counter */}
      {totalCount > 1 && (
        <div className="absolute top-4 left-1/2 -translate-x-1/2 px-4 py-2 rounded-full bg-white/10 text-white text-sm font-medium">
          {currentIndex + 1} / {totalCount}
        </div>
      )}

      {/* Navigation buttons */}
      {totalCount > 1 && (
        <>
          <button
            onClick={onPrevious}
            className="absolute left-4 top-1/2 -translate-y-1/2 p-3 rounded-full bg-white/10 hover:bg-white/20 text-white transition-colors"
            aria-label=""
          >
            <IconWrapper icon={CaretLeft} size={24} weight="bold" />
          </button>
          <button
            onClick={onNext}
            className="absolute right-4 top-1/2 -translate-y-1/2 p-3 rounded-full bg-white/10 hover:bg-white/20 text-white transition-colors"
            aria-label=""
          >
            <IconWrapper icon={CaretRight} size={24} weight="bold" />
          </button>
        </>
      )}

      {/* Media */}
      {isVideo ? (
        <video
          src={attachment.url}
          controls
          autoPlay
          className="max-w-full max-h-[85vh] object-contain"
          onClick={(e) => e.stopPropagation()}
        />
      ) : (
        <img
          src={attachment.url}
          alt={attachment.name}
          className="max-w-full max-h-[90vh] object-contain"
          onClick={(e) => e.stopPropagation()}
        />
      )}

      {/* Filename */}
      <div className="absolute bottom-4 left-1/2 -translate-x-1/2 px-4 py-2 rounded-full bg-white/10 text-white text-sm max-w-[80%] truncate">
        {attachment.name}
      </div>
    </div>
  )
}

export default MixedAttachments
